.swiper {
    width: 100%;
    height: 100%;
  }
  
  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
    padding: 1rem;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }
  .swiper-slide.swiper-slide-active {
    padding: 0;
    margin: 0;
}
  
  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover !important;
  }

  div#feature-product 
  {
      position: relative;
      z-index: 1;
  }  
  .category-section
  {
    .swiper-container
    {
        .swiper-wrapper
        {
            .product.text-center.product-with-qty
            {
                .product-media
                {
                    img
                    {
                        min-height: 350px;
                        height: 350px;
                        width: 100%;
                        max-width: 100%;
                        border-radius: 5px;
                        object-fit: fill !important;
                    }
                }
            }
            .swiper-slide
            {
                background: transparent;
                margin-right: 0px !important;
            }
        }
    }
  }

.category-section
{
    button
    {
        background: var(--base-color);
    }
    .swipper_back_arrow:disabled
    {
        background: grey;
        opacity: 0.5;
    }
    .swipper_front_arrow:disabled
    {
        background: grey;
        opacity: 0.5;
    }
    .swipper_back_arrow
    {
        width: 40px;
        height: 40px;
        border-radius: 50px;
        border: none;
        position: absolute;
        left: 1% !important;
        bottom: 50% !important;
    }
    .swipper_front_arrow
    {
        width: 40px;
        height: 40px;
        border-radius: 50px;
        border: none;
        position: absolute;
        right: 1% !important;
        bottom: 50% !important;
    }
}



div#feature-product
{
    @media screen and (max-width: 767px) 
    {
        .swipper_back_arrow:disabled
        {
            z-index: 99;
        }
        .swipper_front_arrow
        {
            z-index: 99;
        }
    }
}

section.arrivals-section.need_sec
{
    @media screen and (max-width: 767px) 
    {
        .swipper_back_arrow:disabled
        {
            z-index: 99;
        }
        .swipper_front_arrow
        {
            z-index: 99;
        }
    }
}
section.banner-section.row.gutter-no 
{
    @media screen and (max-width: 767px) 
    {
        padding: 0px 15px;
        display: inline-flex;
        justify-content: space-between;
        flex-flow: nowrap;
        overflow-x: scroll;
    } 
}


.widget.widget-newsletter.form-wrapper
{
    .input-wrapper
    {
        @media screen and (max-width: 767px)
        {
            max-width: 30rem;
            text-align: center;
            align-items: center;
            display: flex;
            flex-flow: nowrap;
            padding: 0;
            margin: 0 auto;
        }
    }    
}